.form-contact {
    background-color: rgb(22, 22, 22);
    width: 222vh;
    margin: 0px auto;
    height: 70vh;
}
form {

    width: 80%;
    margin: 0px auto;

}
input {
    width: 100%;
    margin: 10px;
    float: left;
    height: 6vh;
    background-color: rgb(27, 27, 27);
    border: solid 1px rgb(19, 19, 19);
    color: rgb(255, 255, 255);
}
::placeholder {
    color: rgb(255, 255, 255);
}
textarea {
    width: 100%;
    margin: 10px;
    float: left;
    height: 6vh;
    background-color: rgb(27, 27, 27);
    border: solid 1px rgb(19, 19, 19);
    height: 15vh;
    color: rgb(8, 243, 255);


}
h2, p {
color: aliceblue;
}

@media(max-width: 400px){
    form {
   margin-left: -20vh;
   width: 100%;
    }
  }



@media(max-width: 600px){
    form, .form-contact {
   width: 90%;
   margin-left: 17px;
    }
    input,textarea {
        margin-left: -1px;
    }
  }



