body {
  margin: 0;
  font-family: monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(36, 36, 36);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  text-decoration: none;
}





.main-header{
  background: linear-gradient(to bottom, #003447 0%,#001f41 44%,#000000 100%);
  width: 100%;
  height:50px;
  position: fixed;
  margin-top: -25px;
}

nav{
  position: absolute;
  left:0;
  top:0;
  width: 200px;
  top:50px;
  height:calc(100vh - 50px);
  background:#141414;
  transform:translateX(-100%);
  transition:.4s ease;
  color: white;
  border-right: 2px solid rgb(0, 40, 68);


}

.navigation li{
  list-style:none;
  width: 100%;
  border-bottom: 1px solid rgba(0, 121, 207, 0.5);
}

.navigation a{
  color:var(--main-color);
  display: block;
  line-height:3.5;
  padding: 0 10px;
  text-decoration:none;
  transition:.4s ease;
  font-size: 12px;
}

.navigation a:hover{
  transform:translateX(10%);
  color: greenyellow;
}

#btn-nav{
  display: none;
}

#btn-nav:checked ~ nav{
  transform:translateX(0);
}

.btn-nav{
  color:var(--main-color);
  font-size:30px;
  position: absolute;
  left:0;
  top:0;
  cursor:pointer;
  padding:6.5px 15px;
  transition:.2s ease;
}



@media (prefers-reduced-motion: no-preference) {
  .btn-nav{
    animation: App-logo-spin infinite 3s linear;
  }
}

.fa-linkedin, .fa-github, .fa-cog, .fa-folder {
  font-size: 12px;
}








