.col-main {
    flex: 1;  
  }  
  
  .col-complementary {
    flex: 1;  
  }
  
  /* Responsive: */
  
  @media only screen and (min-width: 640px) {
    .layout {
      display: flex;

    }

  }
  
  

  
  .container {
    max-width: 97%;
    margin-right: auto;
    margin-left: auto;
  }
  
  .col {
    padding: 2em;
    margin: 0 10px 10px 0;
    background: #141414;
    color: white;
  }
  

  .projects {

    background-color: #141414;
    width: 95%;
    text-align: center;
    margin: 0px auto;
    color: aliceblue;
    margin-left: 29px;
  }

a {
  color: orange;
  transition: 1s;
}
a:hover {
  color: rgb(47, 255, 0);
  transition: 1s;

}


  
@media only screen and (max-width: 600px) {
  .projects{
    width:     90%;
    margin-left: 14px;
  }
}




