
.App-logo {
  height: 4vmin;
  pointer-events: none;
  margin-top: 50vh;
  border-radius: 50%;
  }

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 3s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(2px + 1vmin);
  color: white;
  background-image: url(https://www.teahub.io/photos/full/237-2374081_ovni-fondo-de-pantalla.jpg);
  background-size: cover;
  background-position: center;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@media only screen and (max-width: 600px) {
  .App-header {
    width: 100%;
  }
}