.faq-drawer {
    margin-bottom: 30px;
    color: #00ffd0;
    background-color: #1d1d1d;
    width: 97%;
    margin: 0px auto;
    margin-left: 14px;
  }
  
  .faq-drawer__content-wrapper {
    font-size: 1.25em;
    line-height: 1.4em;
    max-height: 0px;
    overflow: hidden;
    transition: 0.25s ease-in-out;
    color: #00ffd0;

  }
  
  .faq-drawer__title {
    /*border-top: rgb(0, 255, 234) 1px solid; */
    cursor: pointer;
    display: block;
    font-size: 1.25em;
    font-weight: 700;
    padding: 30px 0 0 0;
  /*  position: relative; *//*la posicion relativa se me interpone al sidenavbar*/
    margin-bottom: 0;
    transition: all 0.25s ease-out;
    margin-left: 10px;
    margin-top: 7px;
  }
  
  .faq-drawer__title::after {
    border-style: solid;
    border-width: 1px 1px 0 0;
    content: " ";
    display: inline-block;
    float: right;
    height: 10px;
    left: -12px;
    position: relative;
    right: 20px;
    top: 2px;
    transform: rotate(135deg);
    transition: 0.35s ease-in-out;
    vertical-align: top;
    width: 10px;
    margin-top: -40px;
  }
  
  /* OPTIONAL HOVER STATE */
  .faq-drawer__title:hover { 
    color: #00ff37  ;
  }
  
  .faq-drawer__trigger:checked
    + .faq-drawer__title
    + .faq-drawer__content-wrapper {
    max-height: 350px;
  }
  
  .faq-drawer__trigger:checked + .faq-drawer__title::after {
    transform: rotate(-45deg);
    transition: 0.25s ease-in-out;
  }
  
  .faq-drawer__trigger[type="checkbox"] {
    display: none;
  }
  
  @media only screen and (max-width: 600px) {
    .container {
      width:     100%;
    }
 .faq-drawer {
    margin-left: -1px;
