
.grid {
    display: grid;
    gap: 10px;
    grid-template-columns: 2fr 1fr;
    font-size: 7px;
  }
  .main {
    background: rgb(22, 22, 22);
    color: rgb(255, 255, 255);
    font-size: 10px;
  }
  .side {
    background: rgb(30, 30, 30);
  }
  .main,
  .side {
    padding: 2%;
    border-radius: 1px
  }
  
  @media(max-width: 600px){
    .grid {
      grid-template-columns: 1fr;
    }
  }


  .security{
    width: 100%;
    height: 86%;
  }

  button {
    background-color: rgb(0, 0, 0);
    border: solid rgb(0, 0, 0);
    height: 7vh;
    margin-top: 10px;
    cursor: pointer;
    color: white;
    transition: 0.8s;
  }


  button:hover {
    background-color: rgb(0, 8, 43);
    border: solid rgb(0, 8, 43);
    height: 7vh;
    margin-top: 10px;
    cursor: pointer;
    color: white;
    transition: 0.7s;
  }
