
.grid {
    display: grid;
    gap: 10px;
    grid-template-columns: 2fr 1fr;
  }
  .main {
    background: rgb(22, 22, 22);
    color: rgb(255, 255, 255);
    font-size: 10px;
  }
  .side {
    background: rgb(30, 30, 30);
  }
  .main,
  .side {
    padding: 2%;
    border-radius: 10px
  }
  
  @media(max-width: 400px){
    .grid {
      grid-template-columns: 1fr;
    }
  }


  .front-end{
    width: 100%;
    height: 86%;
  }